.button{
    border-radius: 12px;
    margin-left:0.2rem;
    padding:0.5rem 2rem;
    color:white;
    font-weight: bold;
    background-color: rgb(202, 22, 202);
    border:1px solid violet;
    
}
.button:hover,
.button:active{
    color:rgb(202, 22, 202);
    font-weight: bold;
    background-color: white;
    border:1px solid rgb(241, 59, 241);
}
.button:disabled,
.button:disabled:hover,
.button:disabled:active{
  cursor: not-allowed;
  background-color: #ccc;
  border-color:#ccc;
  color:  rgb(41 ,41 ,41 );

}