.list{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: white;
    margin: 1rem auto;
    width:40rem;
    max-width: 100%;
   border:1px solid #ccc;
    padding:0.5rem 1rem;
}
.button{
    background-color: maroon;
    padding:0.4rem;
}
.button:hover,
.button:active{
    color: maroon;
    background-color: white;
}
.completion{
    background-color: rgb(30, 78, 30) !important;
    color:white;
}
.colortrue{
    background-color:lightblue;
}
.colorfalse{
    background-color: lightsalmon;
}
.coloryeellow{
    background-color: rgb(235, 235, 121);
}