.forms{
    display: flex;
   flex-direction: row;
   align-items: center;
    justify-content: center;
    
    margin:1rem 1rem;
    margin-top: 2rem;

}
.fullform h2{
    text-align: center;
    font-family: 'Righteous', cursive;
    color:darkblue;
}
.forms.invalid input{
    border:1px solid rgb(255, 135, 135);
    background-color:rgba(243, 219, 219, 0.753);
}
.invalidname{
 
 text-align: left;
 margin-left:12rem;
 

    color:rgb(141, 40, 40);
}

.fullform{
    
    background-color: white;
    max-width: 100%;
    width:40rem;
    margin:5rem auto;
    padding: 0.3rem;
    padding-bottom:3rem;
    border-radius: 12px;
}
.forms input,
.forms select {
    display: block;
    margin:0.3rem;
    border-radius: 12px;
    padding:0.5rem 3rem;
    border:1px solid #ccc;
    background-color:rgba(255, 255, 255, 0.753);
}
.forms input:focus,
.forms select:focus{

    background-color: rgba(233, 239, 241, 0.733);
    border-color:blue;
}
